.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
}

.list {
  list-style: none;
}

ul, li {
  margin: 0;
  padding: 0;
}

.date-field {
  max-width: 151px;
  height: 31px;
  border: 1px solid #111321;
  border-radius: 20px;
  justify-content: space-between;
  align-items: center;
  padding: 5px 12px;
  display: flex;
}

.data_selected {
  max-width: 66px;
  height: 21px;
  letter-spacing: .02em;
  color: #111321;
  opacity: .4;
  font-family: Manrope;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  display: block;
}

.btn_calendar_open {
  width: 20px;
  height: 17px;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  display: block;
}

.btn_calendar_close {
  width: 20px;
  height: 17px;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  display: none;
}

.calendar {
  width: 250px;
  height: 250px;
  background-color: #f8f8f8;
  border-radius: 20px;
  padding: 27px 16px 20px;
  box-shadow: 0 10px 60px rgba(0, 0, 0, .1);
}

.calendar_month_next {
  margin-left: 8px;
  margin-right: auto;
  transform: rotate(90deg);
}

.calendar_year_next {
  transform: rotate(90deg);
}

.calendar_year_back {
  margin-right: 33px;
  transform: rotate(-90deg);
}

.calendar_title {
  justify-content: center;
  align-items: center;
  display: flex;
}

.month, .year {
  color: #000;
  font-family: Manrope;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.38;
}

.month {
  margin-right: 5px;
}

.week_days {
  text-align: center;
  color: rgba(60, 60, 67, .3);
  justify-content: space-around;
  gap: 17px;
  margin-top: 13px;
  font-family: Manrope;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  display: flex;
}

.days {
  text-align: center;
  justify-content: space-around;
  gap: 17px;
  margin-top: 14px;
  font-family: Manrope;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  display: flex;
}

